  .expand-transition {
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
  height: 45px;
  margin: 0;
  position: relative;
  left: 50px;
  overflow: hidden;
}
  /* .expand-enter 定义进入的开始状态 */
  /* .expand-leave 定义离开的结束状态 */
  .expand-enter, .expand-leave {
    height: 0;
    opacity: 0;
  }



